.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: radial-gradient(circle, #121212, #2c4a3e);
  padding: 50px;
  gap: 20px;
  margin: 50px 0;
  border-radius: 10px;
}

.about-card {
  width: 100%;
  background: linear-gradient(90deg, #ffffff, #1c2a3a);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

.background-pic {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.profile-pic {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  position: absolute;
  bottom: -50px;
  left: 20px;
  border: 3px solid #faf9f6;
}

.about-details {
  padding: 70px 20px 20px 20px;
  text-align: left;
}

.about-details h3 {
  margin: 10px 0;
  font-family: 'Roboto', sans-serif;
  color: #121212;
  font-weight: 700;
  font-size: 2.5em;
}

.about-details p {
  margin: 5px 0 20px;
  font-family: 'Roboto', sans-serif;
  color: #1c2a3a;
  font-weight: 400;
  font-size: 1.5em;
}

.about-details ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.about-details ul li {
  margin-bottom: 10px;
}

.about-details ul li a {
  color: #1c2a3a;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.3em;
  transition: color 0.3s;
}

.about-details ul li a:hover {
  color: #121212;
}

.about-content {
  flex: 1;
  width: 100%;
  background: linear-gradient(45deg, #ffffff 60%, #1c2a3a);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-content h2 {
  margin-bottom: 20px;
  font-family: 'Roboto', sans-serif;
  color: #121212;
  font-weight: 700;
  font-size: 2.5em;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid #1c2a3a;
  padding-bottom: 10px;
}

.about-content p {
  font-family: 'Roboto', sans-serif;
  color: #1c2a3a;
  line-height: 1.6;
  font-weight: 400;
  margin-bottom: 20px;
  transition: transform 0.3s;
  font-size: 1.5em;
}

.about-content p:hover {
  transform: translateX(10px);
}

@media (max-width: 767px) {
  .about {
    padding: 20px;
    margin: 20px 0;
    padding: 0px 0;
  }

  .about-card {
    width: 100%;
  }

  .background-pic {
    height: 200px;
  }

  .profile-pic {
    width: 100px;
    height: 100px;
    bottom: -25px;
    left: 10px;
  }

  .about-details {
    padding: 20px;
  }

  .about-details h3 {
    font-size: 1.5em;
  }

  .about-details p {
    font-size: 1em;
  }

  .about-details ul li a {
    font-size: 1em;
  }

  .about-content {
    padding: 0;
  }
  .about-content h2 {
    font-size: 1.5em;
    margin-left: 10px;
  }

  .about-content p {
    font-size: 1em;
    margin-left: 10px;
    margin-right: 10px;
  }
  .about-content {
  }
}

@media (min-width: 768px) {
  .about {
    flex-direction: row;
  }

  .about-card {
    width: 50%;
  }

  .about-content {
    max-width: 600px;
  }
}
